import ParamsTable from '@/components/ParamsTable.vue';
export default {
  name: 'Media',
  components: {
    ParamsTable: ParamsTable
  },
  data: function data() {
    return {
      responsiveness: 'is-responsive',
      markup: "<img class=\"is-responsive\" />"
    };
  },
  watch: {
    responsiveness: function responsiveness(val) {
      this.markup = "<img class=\"".concat(val, "\" />");
    }
  }
};